function sortData(data) {
    return data.sort((a, b) => b.value - a.value);
}

function removeCrimeTypesWithNoData(data) {
    return data.filter(d => d.value);
}

function prepareCrimeTypeData(data, year, crime_code) {
    var crime_type = []
    data.forEach(d => {
        if(d.Year == year && d.Crime_Code_Description == crime_code) {
            crime_type[d.Area_ID] = d.Count;
        }
    });
    return crime_type
}

function prepareData(data, area) {
    let yearMap = new Map()
    data.forEach(d => {
        if (area == null || d.Area_ID == area) {
            if (!Number.isInteger(+d.Year)) { return; }
            if (!yearMap.has(d.Year)) {
                yearMap.set(d.Year, new Map());
            }
            let crimeMap = yearMap.get(d.Year)
            if (!crimeMap.has(d.Crime_Code_Description)){
                crimeMap.set(d.Crime_Code_Description, 0);
            }
            crimeMap.set(d.Crime_Code_Description, crimeMap.get(d.Crime_Code_Description) + (+d.Count));
        }
    });

    result = new Array()
    yearMap.forEach((crimeMap, year, map) => {
        crimeMap.forEach((value, crime, map) => {
            const newEntry = {
                value: value,
                crimeType: crime
            }
            if(!result[year]) {
                result[year] = new Array();
            }
            result[year].push(newEntry);
        });
    });
    return result
}

function xAccessor(d) {
    return d.value;
}

function yAccessor(d) {
    return d.crimeType;
}

function getXScale(minVal, maxVal) {
    return d3.scaleLinear()
        .domain([minVal, maxVal])
        .range([0, width])
}

var yScale = d3.scaleBand()
    // .rangeRound([0, height], 0.1)
    .rangeRound([topPadding, height - topPadding], 0.1)
    .padding(0.1);

function drawXAxis(el) {
    let axis = el.select('.axis--x');
    axis.remove()
    el.append('g')
        .attr('class', 'axis--x')
        // .attr('transform', `translate(${leftPadding},${height})`)
        .attr('transform', `translate(${leftPadding},${height - topPadding})`)
        .call(d3.axisBottom(getXScale(0, maxVal + (maxVal / 4))));
    el.append("text")
        .attr("text-anchor", "middle")  // this makes it easy to centre the text as the transform is applied to the anchor
        .attr("transform", "translate("+ (width/2) +","+460+")")  // centre below axis
        .style("font-size", "11px")
        .text("Number of Crimes");
}

function drawYAxis(el, data, t) {
    let axis = el.select('.axis--y');
    if (axis.empty()) {
        axis = el.append('g')
        .attr('class', 'axis--y');
}

axis.transition(t)
    .call(d3.axisLeft(yScale))
    .selectAll('g')
    .delay(delay);
}

