function setXBoundaries(selectedData){
    if (selectedData == null) {
        minVal = overAllMinVal
        maxVal = overAllMaxVal
    } else {
        minVal = getMin(selectedData)
        maxVal = getMax(selectedData)
    }
}

function getMin(selectedData) {
    var min = Number.MAX_SAFE_INTEGER
    selectedData.forEach((data) => {
        if (data.value < min) {
            min = data.value
        }
    });
    return min
}

function getMax(selectedData) {
    var max = Number.MIN_SAFE_INTEGER
    selectedData.forEach((data) => {
        if (data.value > max) {
            max = data.value
        }
    });
    return max
}

fetch('https://raw.githubusercontent.com/UW-CSE442-WI20/A3-crime-gun-violence-in-the-us/master/src/data/areabycrime.csv')
.then((res) => res.text())
.then((res) => {
    rawData = d3.csvParse(res)
    data = prepareData(rawData);
    const years = Object.keys(data).map(d => +d);
    year = years[0];
    selectedData = removeCrimeTypesWithNoData(sortData(data[year]))
    overAllMinVal = getMin(selectedData)
    overAllMaxVal = getMax(selectedData)
    setXBoundaries(null)
    let crimeTypes = selectedData.map(yAccessor);

    yScale.domain(crimeTypes);
    drawXAxis(svg, selectedData);
    drawYAxis(svg, selectedData);
    drawBarsByYear(svg, selectedData);
    drawMapByYear(selectYear);

    d3.select("#year").selectAll("option")
		.data(years)
	    .enter().append("option")
        .attr("value", function(d){
            return d;
        })
        .text(function(d){
            return d;
        });
});

//Slider stuff
var dataTime = d3.range(0, 8).map(function(d) {
    return new Date(2010 + d, 01, 01);
});

var currentYear = 2010;

function createSliderTime() {
    return d3
    .sliderBottom()
    .min(d3.min(dataTime))
    .max(d3.max(dataTime))
    .step(1000 * 60 * 60 * 24 * 365)
    .width(450)
    .tickFormat(d3.timeFormat('%Y'))
    .tickValues(dataTime)
    .default(new Date(2010, 01, 01))
    .handle(
        d3.symbol()
            .type(d3.symbolCircle)
            .size(200)()
    )
    .on('onchange', val => {
        year = +(d3.timeFormat('%Y')(val))
        d3.select('#value-time').text(d3.timeFormat('%Y')(val));
        d3.select('#year-name').text(d3.timeFormat('%Y')(val));
        const t = d3.transition().duration(150);
        selectYear = year

        selectedData = removeCrimeTypesWithNoData(sortData(data[year]));

        yScale.domain(selectedData.map(yAccessor));
        drawXAxis(svg, selectedData);
        drawYAxis(svg, selectedData, t);
        drawBarsByYear(svg, selectedData, t);
        drawMapByYear(selectYear);
    });
}

var sliderTime = createSliderTime();
var gTime = d3
    .select('div#slider-time')
    .append('svg')
    .attr("id", 'slider-svg')
    .attr('width', 800)
    .attr('height', 100)
    .append('g')
    .attr('transform', 'translate(30,10)')
gTime.call(sliderTime);

d3.select('p#value-time').text(d3.timeFormat('%Y')(sliderTime.value()));
d3.select('#year-name').text(d3.timeFormat('%Y')(sliderTime.value()));

// play button

var moving = false;
var targetValue = 1000;

var playButton = d3.select("#play-button");

playButton
    .on("click", function () {
        var button = d3.select(this);
        currentYear = +year
        updateCharts(currentYear);
        console.log("Current Year " + currentYear)
        if (button.text() == "Pause") {
            moving = false;
            clearInterval(timer);
            button.text("Play Overall Crime Viz");
            document.getElementById("slider-time").style.visibility = "visible";
        } else {
            moving = true;
            timer = setInterval(step, 1000);
            button.text("Pause");
            document.getElementById("slider-time").style.visibility = "hidden";
        }
        console.log("Slider moving: " + moving);
    })

function step() {
    currentYear += 1
    if (currentYear <= 2017) {
        updateCharts(currentYear);
    } else {
        moving = false;
        currentYear = +year;
        d3.select('p#value-time').text(d3.timeFormat('%Y')(sliderTime.value()));
        d3.select('#year-name').text(d3.timeFormat('%Y')(sliderTime.value()));
        document.getElementById("slider-time").style.visibility = "visible";
        updateCharts(currentYear);
        clearInterval(timer);
        playButton.text("Play Overall Crime Viz");
        console.log("Slider moving: " + moving);
    }
}

function updateCharts(givenYear, updateMap = true) {
    d3.select('#value-time').text(givenYear);
    d3.select('#year-name').text(givenYear);

    const t = d3.transition().duration(400);

    selectedData = removeCrimeTypesWithNoData(sortData(data[givenYear]));

    yScale.domain(selectedData.map(yAccessor));
    drawXAxis(svg, selectedData);
    drawYAxis(svg, selectedData, t);
    drawBarsByYear(svg, selectedData, t);
    if (updateMap) {
        drawMapByYear(givenYear)
    }
}


